.content-news {
    padding: 2em;
    display: flex;
    flex-direction: column;

    gap: 2em;
}

.news-card {
    width: 100%;
    height: 240px;

    border-radius: 0.5em;

    background-color: #EBEBEB;
    position: relative;

    overflow: clip;

}


.news-card-title {
    width: 100%;
    position: absolute;
    padding: 1.5em 1em;
    bottom: 0;
    
    color: black;
}

.news-card-title > h1 {
    margin-bottom: 0.5em;
    font-size: 1.5em;
}

.news-card-title > small {
    font-size: 0.8em;
    opacity: 0.75;
}

.line {
    max-width: 100%;
    width: 280px;
    height: 2px;
    background-color: #9DD4D8;
}

.news-article {
    background-color: #EBEBEB;
    width: 100%;

    border-radius: 0.5em;

    padding: 4em 3em;
}

.news-article > .line {
    height: 3px;
}

.news-article > h1{
    font-size: 3.5em;
    margin-bottom: 0.35em;
}

.news-article > p {
    font-size: 1.5em;
    margin-top: 4em;
}

